@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
button {
  background-color: white;
  color: #FC8E30;
  min-width: 200px;
  font-size: 24px;
  font-weight: 800;
  border-radius: 15px;
  margin: 5px;
  border: 5px solid #FC8E30;
  outline: none;
}

button:hover {
  background-color: #FC8E30;
  color: white;
  cursor: pointer;
  /* border-color: white; */
}
.highscore-table {
  width: 100%;
  background-color: black;
}

.col {
  border: 1px solid black;
  padding: 1px 8px;
}

td {
  background-color:  white;
  margin: 5px;
  height: 50px;
}

th {
  background-color: #FC8E30;
  height: 25px;
}
.category-select {
  height: 30px;
  font-family: 'Roboto';
  outline: none;
  max-width: 100%;
  margin-bottom: 20px;
}
.option-label {
  text-align: left;
  font-size: 18px;
  font-weight: 500;
}

.option-input {
  margin: auto 0;
  width: 15px;
  height: 15px;
}
.question {
  margin: 0;
  font-size: 24px;
}
.question-vs-answer {
  text-align: left;
  width: 100%;
}



.username-input-wrapper {
  margin: 80px 0 40px;
}

.user-label, .user-input {
  height: 30px;
  font-family: 'Roboto';
  margin: 0 10px;
  
}

.user-label {
  font-size: 16px;
  font-weight: 500;
}

.user-input {
  padding: 3px 10px;
  outline: none;
}
.App {
  text-align: center;
}
body {
  margin: 0;
  font-family: 'Roboto';
  background-color: #FCD46D;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

